import * as React from "react"
export const Breadcrumb: React.FC<{ breadcrumbs?: Array<{ title: string; href: string }>; currentPage: string }> = ({
  breadcrumbs,
  currentPage,
}) => {
  return (
    <div className="uk-text-center">
      <ul className="uk-breadcrumb uk-flex-center uk-margin-remove">
        <li>
          <a href="/">خانه</a>
        </li>
        {breadcrumbs &&
          breadcrumbs.map(breadcrumb => (
            <li key={breadcrumb.title}>
              <a href={breadcrumb.href}>{breadcrumb.title}</a>
            </li>
          ))}
        {currentPage && (
          <li>
            <span>{currentPage}</span>
          </li>
        )}
      </ul>
    </div>
  );
};
